import { detectIOS } from "@vkontakte/vkjs";
import type { Version } from "../types";
import { noop } from "./utils";

export enum System {
  IOS = "ios",
  UNKNOWN = "",
}

export interface BrowserInfo {
  userAgent: string;
  system: System;
  systemVersion: Version | null;
}

const memoized: { [index: string]: BrowserInfo } = {};

export function computeBrowserInfo(userAgent = ""): BrowserInfo {
  if (memoized[userAgent]) {
    return memoized[userAgent];
  }

  let systemVersion: Version | null = null;
  let system = System.UNKNOWN;

  const { isIOS, iosMajor, iosMinor } = detectIOS(userAgent);

  if (isIOS) {
    system = System.IOS;
    systemVersion = {
      major: iosMajor,
      minor: iosMinor,
    };
  }

  const browserInfo: BrowserInfo = {
    userAgent,
    system,
    systemVersion,
  };

  memoized[userAgent] = browserInfo;

  return browserInfo;
}

/**
 *   `window.matchMedia`  SSR.
 *
 *        SSR.
 */
export function mediaQueryNull(query: string): MediaQueryList {
  console.error(`[mediaQueryNull] ,    \`Window.matchMedia()\` API  .

  ,      :  SSR     .

 CSS Media Query     https://github.com/artsy/fresnel.`);
  return {
    matches: false,
    media: query,
    onchange: noop,
    addListener: noop,
    removeListener: noop,
    addEventListener: noop,
    removeEventListener: noop,
    dispatchEvent() {
      return false;
    },
  };
}
